<template>
  <div>
    <div class="page-title">
      <h3>Отрядная статистика</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <p>Выберите период:</p>
      <select style="display: block" v-model="choosed" ref="sq">
        <option v-for="d of period" :key="d.id" :data-id="d.id">
          {{ d.title }}
        </option>
      </select>
      <p>Выберите отряд:</p>
      <select style="display: block" v-model="tag">
        <option v-for="s of squads" :key="s">{{ s }}</option>
      </select>
      <Loader v-if="loading" />
      <v-table v-else :data="n">
        <thead slot="head">
          <th sortKey="id">Место</th>
          <v-th sortKey="playerName">Ник</v-th>
          <v-th sortKey="totalPlayedGames">Кол-во игр</v-th>
          <v-th sortKey="kills">Убийств</v-th>
          <v-th sortKey="teamkills">Тимкиллов</v-th>
          <v-th sortKey="deaths">Смертей</v-th>
          <v-th sortKey="kdRatio">K/D</v-th>
          <v-th sortKey="totalScore" defaultSort="desc">Счет</v-th>
        </thead>
        <tbody slot="body" slot-scope="{ displayData }">
          <tr v-for="(row, index) in displayData" :key="row.id">
            <td>{{ index + 1 }}</td>
            <td>
              <router-link
                :to="{ name: 'Player', params: { nick: row.playerName } }"
                >{{ row.lastSquadPrefix + row.playerName }}</router-link
              >
            </td>
            <td>{{ row.totalPlayedGames }}</td>
            <td>{{ row.kills }}</td>
            <td>{{ row.teamkills }}</td>
            <td>{{ row.deaths }}</td>
            <td>{{ row.kdRatio }}</td>
            <td>{{ row.totalScore }}</td>
          </tr>
        </tbody>
      </v-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data: () => ({
    n: [],
    tag: "[FNX]",
    squads: [],
    loading: true,
    choosed: "За все время",
    period: [],
  }),
  async mounted() {
    this.squads.push("[Одиночки]");
    let s = await fetch("stats.json");
    s = await s.json();
    for (let key in s.squadStatistics) {
      this.squads.push(s.squadStatistics[key].prefix);
    }
    let r = await fetch("rotations_stats.json");
    r = await r.json();
    for (let key in r) {
      let tStartDate = r[key].startDate;
      let tEndDate = r[key].endDate;
      r[key].startDate = new Date(r[key].startDate).toLocaleString("ru", {
        year: "numeric",
        month: "short",
        day: "numeric",
      });
      if (r[key].endDate === null) {
        r[key].endDate = " по настоящий момент.";
      } else {
        r[key].endDate = new Date(r[key].endDate).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
      }
      this.period.push({
        id: Number(key),
        title: `${r[key].startDate} - ${r[key].endDate}`,
        endDate: tEndDate,
        startDate: tStartDate,
      });
    }
    this.period.push({
      id: r.length,
      title: "За все время",
    });
    this.period.reverse();
    await this.refresh();
  },
  watch: {
    tag: function (newt, old) {
      this.n = [];
      this.refresh();
    },
    choosed: function () {
      this.n = [];
      this.refresh();
    },
  },
  methods: {
    async refresh() {
      if (this.choosed == this.period[0].title) {
        this.r();
      } else {
        this.rotationTable();
      }
    },
    async r() {
      this.loading = true;
      this.n = [];
      let r = await fetch("stats.json");
      r = await r.json();
      let id = 1;
      for (let key in r.globalStatistics) {
        if (r.globalStatistics[key].lastSquadPrefix === this.tag) {
          r.globalStatistics[key].id = id;
          id++;
          this.n.push(r.globalStatistics[key]);
        } else if (
          r.globalStatistics[key].lastSquadPrefix === null &&
          this.tag == "[Одиночки]"
        ) {
          r.globalStatistics[key].lastSquadPrefix = "";
          this.n.push(r.globalStatistics[key]);
        }
      }
      this.loading = false;
    },
    async rotationTable() {
      this.loading = true;
      this.n = [];
      let r = await fetch("rotations_stats.json");
      r = await r.json();
      let id = 1;
      let rotationID = this.$refs.sq.selectedIndex;
      for (let key in r) {
        if (r[key].startDate == this.period[rotationID].startDate) {
          for (let key1 in r[key].stats.global) {
            if (r[key].stats.global[key1].lastSquadPrefix === this.tag) {
              this.n.push(r[key].stats.global[key1]);
            } else if (
              r[key].stats.global[key1].lastSquadPrefix === null &&
              this.tag == "[Одиночки]"
            ) {
              r[key].stats.global[key1].lastSquadPrefix = "";
              this.n.push(r[key].stats.global[key1]);
            }
          }
        }
      }
      this.loading = false;
    },
  },
};
</script>
